<template>
  <div>
    <group>
      <x-address @on-hide="logHide" @on-show="logShow" :title="title" v-model="value" :list="addressData" placeholder="" inline-desc="placeholder"></x-address>
      <cell title="value" :value="value"></cell>
    </group>
    <br>
    <group>
      <x-address :title="title2" v-model="value2" raw-value :list="addressData"></x-address>
    </group>
    <br/>
    <div style="padding: 0 15px;">
      <x-button type="primary" @click.native="changeData"></x-button>
    </div>
    <br/>

    <group>
      <x-address title="" v-model="value3" raw-value :list="addressData"></x-address>
    </group>
    <group>
      <x-address title="" v-model="value4" raw-value :list="addressData" hide-district></x-address ref="demo2">
      <cell title="value" :value="value4"></cell>
      <cell title="" :value="getName(value4)"></cell>
    </group>

    <br/>
    <group title="">
      <x-address title="" v-model="value5" raw-value :list="addressData" inline-desc=",  , "></x-address>
    </group>
  </div>
</template>

<script>
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import value2name from 'vux/src/filters/value2name'
export default {
  components: {
    Group,
    XAddress,
    XButton,
    Cell
  },
  data () {
    return {
      title: '',
      value: [],
      title2: '',
      value2: ['', '', ''],
      value3: ['', '', '--'],
      addressData: ChinaAddressData,
      value4: [],
      value5: ['', ' ', '']
    }
  },
  methods: {
    changeData () {
      this.value2 = ['430000', '430400', '430407']
    },
    getName (value) {
      return value2name(value, ChinaAddressData)
    },
    logHide (str) {
      console.log('on-hide', str)
    },
    logShow (str) {
      console.log('on-show')
    }
  }
}
</script>



